*{
    box-sizing: border-box;
}
#buttons {
    position:fixed;
    top:0;
    right:0;
    z-index: 10;
    display: flex;
    flex-direction: column;
    margin-top:10px;
    margin-right: 10px;
}
#buttons>button{
    margin-bottom: 10px;
    padding: 4px 8px;
}
#demo{
    display: none ;
}
#demo2{
    position:fixed;
    top:0;
    left:0;
    width: 100%;
    height:50vh;
    overflow-y: auto;
}
#demo2::-webkit-scrollbar {
    display: none;
}
#html{
    position: fixed;
    left:0;
    bottom: 0px;
    width: 100%;
    height: 50vh;
}